#popdown-palette {
    display: none;
    position: absolute;
    z-index: 9999;

    top: 0;
    left: 0;
    width: 100%;
    max-height: 100%;
    overflow-y: scroll;

    background: rgba(255, 255, 255, 0.85);

    &:last-child {
        margin-bottom: 2.5em; }

    &.show {
        display: block; }

    &.show ~ .canvasHolder {
        filter: blur(10px);
        -webkit-filter: blur(10px); }

    ul {
        display: none; }
    .palette.show ul {
        display: block; }

    li {
        list-style: none; }

    h2 {
        border-bottom: 1px solid #444;
        width: 80%;
        padding: 0 10%;

        span {
            vertical-align: 60%; }

        img {
            display: block;
            float: right; } }

    .palette.show .show-button {
        display: none; }

    .palette:not(.show) {
        .hide-button, .popout-button {
            display: none; } } }

div.back {
    display: block;
    position: fixed;

    height: 2.5em;
    width: 100%;
    top: calc(100% - 2.5em);

    background-color: #999;

    h2 {
        margin-top: 0.25em;
        border-bottom: none !important;
        color: white; }

    &:active {
        background-color: black; } }
